@import '~node_modules/modern-normalize/modern-normalize.css';
.container {
  margin-left: auto;
  margin-right: auto;
  padding: 0 15px;
}
.gallery-section {
  background-color: #fff;
}
.container {
  max-width: 480px;
}
.container__images {
  max-width: 480px;
  border-radius: 10%;
  display: grid;
  align-items: center;
  justify-content: center;
  gap: 1%;
  grid-template-columns: 1fr 1fr 2fr 1fr 1fr 1fr;
}
.container__images.container__first {
  grid-area: first;
}
.container__images.container__second {
  grid-area: second;
}
.container__images.container__third {
  grid-area: third;
}
.container__images.container__fourth {
  grid-area: fourth;
}
.container__images.container__fifth {
  grid-area: fifth;
}
.container__images.container__sixth {
  grid-area: sixth;
}
.container__image {
  max-width: 100%;
  height: 100%;
}
.container__img1:hover,
.container__img2:hover,
.container__img4:hover {
  transform: scale(1.2) translate(-20px, -20px);
  transition: transform 0.25s ease-in-out;
  transition: cubic-bezier(0.3, 0, 0.3, 1) 250ms;
}
.container__img6:hover,
.container__img8:hover,
.container__img10:hover {
  transform: scale(1.2) translate(20px, -20px);
  transition: transform 0.25s ease-in-out;
  transition: cubic-bezier(0.3, 0, 0.3, 1) 250ms;
}
.container__img7:hover,
.container__img9:hover {
  transform: scale(1.2) translate(20px, 20px);
  transition: transform 0.25s ease-in-out;
  transition: cubic-bezier(0.3, 0, 0.3, 1) 250ms;
}
.container__img10:hover {
  transform: scale(1.2) translate(20px, 0px);
  transition: transform 0.25s ease-in-out;
  transition: cubic-bezier(0.3, 0, 0.3, 1) 250ms;
}
.container__img1:hover {
  transform: scale(1.2) translate(-20px, 0px);
  transition: transform 0.25s ease-in-out;
  transition: cubic-bezier(0.3, 0, 0.3, 1) 250ms;
}
.container__img3:hover,
.container__img5:hover {
  transform: scale(1.2) translate(-20px, 20px);
  transition: transform 0.25s ease-in-out;
  transition: cubic-bezier(0.3, 0, 0.3, 1) 250ms;
}
.container__img3 {
  padding-bottom: 30%;
}
.container__img9 {
  padding-bottom: 30%;
}
@media screen and (min-width: 481px) {
  .container {
    max-width: 768px;
  }
  .container__images {
    max-width: 768px;
  }
}
@media screen and (min-width: 769px) {
  .container {
    max-width: 1200px;
  }
  .container__images {
    max-width: 1013px;
  }
}
.container {
  margin-left: auto;
  margin-right: auto;
  padding: 0 15px;
}
.container__box {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 60px;
}
.slideshow-container {
  background: #fafafa;
  position: relative;
  margin-top: 32px;
}
.mySlides {
  z-index: 950;
  display: none;
  padding: 47px 61px;
  text-align: center;
}
.mySlides {
  display: none;
}
.author {
  margin-top: 36px;
  font-weight: 700;
  font-size: 14px;
  line-height: 186%;
}
.prev,
.next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -30px;
  padding: 16px;
  color: #888;
  font-weight: bold;
  font-size: 20px;
  border-radius: 0 3px 3px 0;
  user-select: none;
}
.next {
  position: absolute;
  right: 0;
  border-radius: 3px 0 0 3px;
}
.prev:hover,
.next:hover {
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
}
.dot-container {
  text-align: center;
  padding: 20px;
}
.dot {
  cursor: pointer;
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}
.active,
.dot:hover {
  background-color: #717171;
}
.container__text {
  display: inline;
  z-index: 950;
  position: relative;
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 260%;
  text-align: center;
  letter-spacing: 0.04em;
  border-bottom: 1px solid #e1e1e1;
  padding-bottom: 3px;
}
.container__character {
  position: absolute;
  top: 30px;
  left: 53;
  z-index: 900;
}
@media screen and (min-width: 481px) {
  .container {
    max-width: 768px;
  }
  .container__text {
    font-size: 14px;
    line-height: 214%;
  }
  .author {
    color: #000;
    font-size: 16px;
    line-height: 186%;
  }
}
@media screen and (min-width: 769px) {
  .container {
    max-width: 1200px;
  }
  .container__text {
    font-size: 16px;
    line-height: 186%;
  }
  .author {
    color: #000;
    font-size: 20px;
    line-height: 186%;
  }
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
* {
  margin: 0;
}
body {
  margin: 0 auto;
  font-family: 'DM Sans', sans-serif;
}
h1,
h2,
h3,
h4,
h5 {
  font-weight: bold;
  font-family: 'Titan One', cursive;
} 